import React, { useEffect, useState } from "react";
import ListPage from "../components/ListPage";
import styles from "./QuestionListPage.module.css";
import searchImg from "../assets/search.svg";
import QuestionItem from "./../components/QuestionItem";
import { getDatas } from "../api/firebase";

let listItems;

function QuestionListPage(props) {
  const [items, setItems] = useState([]);

  const handleLoad = async () => {
    const resultData = await getDatas("questions");
    listItems = resultData; //      .
    setItems(resultData);
  };

  useEffect(() => {
    handleLoad();
  }, []);

  return (
    <ListPage variant="community">
      <form className={styles.form}>
        <input placeholder="  " />
        <button>
          <img src={searchImg} />
        </button>
      </form>
      <p className={styles.count}> {items.length} </p>
      <div className={styles.questionList}>
        {items.map((question) => (
          <QuestionItem key={question.docId} question={question} />
        ))}
      </div>
    </ListPage>
  );
}

export default QuestionListPage;
