import React, { useState } from "react";
import { Link, useLocation, useNavigate } from "react-router-dom";
import styled from "styled-components";
import Input from "./Input";
import LoginButton from "./LoginButton";
import KakaoButton from "./KakaoButton";
import { deleteField } from "firebase/firestore";
import { getMember } from "../api/firebase";

const LoginContainer = styled.div`
  width: 400px;
  margin: 40px auto;

  ${LoginButton} {
    width: 100%;
    margin: 8px 0;
  }

  ${Input} {
    margin-bottom: 16px;
  }
`;

const Logo = styled.h1`
  font-family: Pretendard;
  text-align: center;
  font-size: 40px;
  background-image: linear-gradient(135deg, aqua, purple);
  background-clip: text;
  color: transparent;
`;

const Description = styled.div`
  color: #848187;
  text-align: center;
`;

const Label = styled.label`
  color: #e1c6f7;
`;

function Login(props) {
  const { state } = useLocation();
  const navigate = useNavigate();
  const [values, setValues] = useState({ email: "", password: "" });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setValues((prevValues) => ({
      ...prevValues,
      [name]: value,
    }));
  };

  const handleLogin = async (e) => {
    //  submit  
    e.preventDefault();
    //       member .
    const { memberObj, message } = await getMember(values);
    if (Object.keys(memberObj).length === 0) {
      // 
      alert(message);
    } else {
      // 
      window.localStorage.setItem("member", JSON.stringify(memberObj));
      alert(message);
      navigate(state ? state : "/", { replace: true });
    }
  };

  return (
    <LoginContainer>
      <Logo>DW </Logo>
      <Description>
         ? <Link> </Link>
      </Description>
      <form onSubmit={handleLogin}>
        <Label htmlFor="email"></Label>
        <Input
          type="email"
          id="email"
          placeholder="styled@DW.kr"
          onChange={handleChange}
          name="email"
        />
        <Label htmlFor="password"></Label>
        <Input
          type="password"
          id="password"
          placeholder=""
          onChange={handleChange}
          name="password"
        />
        <LoginButton> </LoginButton>
      </form>
      <KakaoButton> </KakaoButton>
    </LoginContainer>
  );
}

export default Login;
