import React, { useEffect, useState } from "react";
import ListPage from "../components/ListPage";
import searchImg from "../assets/search.svg";
import styles from "./CourseListPage.module.css";
import CourseItem from "../components/CourseItem";
import { getDatas } from "../api/firebase";
import Warn from "./../components/Warn";

let listItems;

function CourseListPage(props) {
  const [items, setItems] = useState([]);
  const [inputValue, setInputValue] = useState("");
  const [isLoading, setIsLoading] = useState(true);

  //input 
  const handleKeyWordChange = (e) => {
    setInputValue(e.target.value);
    // console.log(inputValue);
    //    state .
  };

  //submit 
  const handleSubmit = (e) => {
    e.preventDefault();
    //    listItems  
    //    title       .
    // const result = listItems.filter((item) => item.title.match(inputValue)); //includes  
    //   items state  set .
    // setItems(result);

    setItems(listItems.filter(({ title }) => title.includes(inputValue)));
  };

  const handleLoad = async () => {
    setIsLoading(true);
    //  courses   .
    const data = await getDatas("courses");
    //   
    listItems = data;
    //    .
    // console.log(data);
    // items state  set .
    setItems(data);
    setIsLoading(false);
  };

  useEffect(() => {
    handleLoad();
  }, []);

  return (
    <ListPage variant="catalog">
      <form className={styles.form} onSubmit={handleSubmit}>
        <input
          placeholder="  "
          onChange={handleKeyWordChange}
        />
        <button>
          <img src={searchImg} alt="" />
        </button>
      </form>
      <p className={styles.count}> {items.length} </p>

      {items.length === 0 && !isLoading ? (
        <Warn
          className={styles.emptyList}
          title="   ."
          description="       ."
        />
      ) : (
        <div className={styles.courseList}>
          {items.map((item, idx) => {
            return <CourseItem key={idx} Data={item} />;
          })}
        </div>
      )}
    </ListPage>
  );
}

export default CourseListPage;
